import {createStore} from 'vuex'

export const store = createStore({
    state: {
        notes: [
            {title: 'Learn Vue 3', tags: ['work']},
            {title: 'Finish course', tags: ['work', 'home']},
        ],
        value: '',
        item: ['home', 'work', 'travel']
    },
    getters: {
        getTags(state) {
            return state.item
        },
        getNotes(state) {
            return state.notes
        },
        // getUser(state) {
        //     return state.user
        // },
        // getAllUsers(state) {
        //     return state.users
        // },
        // getUsers(state) {
        //     return state.users.filter(user => !user.admin)
        // },
        // getUsersLength(state, getters) {
        //     const suffix = 'Кол-во пользователей: '
        //     return `${suffix} ${getters.getAllUsers.length}`
        // },
        // getUserById: state => id => state.users.find(user => user.id == id)
    },
    mutations: {
        setUser(state, user) {
            state.user = user
        }
    },
    actions: {
        setUser ({ commit }) {
            setTimeout(()=> {
                const fakeUser = {
                    id: 12314,
                    name: 'Dmitry',
                    admin: false
                }
                commit('setUser', fakeUser)
            }, 1000)
        }
    }
})


